import react from 'react'

export const Alert = ({ type }) => {
    
  return (
    <div className={`alert ${type === 'success' ? 'alert-success' : 'alert-danger'}`} role="alert">
      {type === 'success' ? (
        <>
          <h5 class="alert-heading">,   !</h5>
          <span>H     !</span>
        </>
      ) : (
        <>
          <h5 class="alert-heading"> !</h5>
          <span>     !</span>
        </>
      )}
    </div>
  )
}
